<template>
	<view class="person_wrap">
		<template v-if="personList.length>0">
			<view class="personList" v-for="(item,index) in personList" :key="index">
				<view class="listTop">
					<view class="listLeft">
						<view class="nameTag">
							<text class="custname">{{item.custName}}</text>
							<text class="tags" v-if="item.intensionLevelName">{{item.intensionLevelName}}</text>
							<text class="tags bule" v-if="item.stateName">{{item.stateName}}</text>
							<text class="tags red" v-if="item.timeOverdue">{{'逾期'+item.timeOverdue+'天'}}</text>
						</view>
						<view class="custMobile">{{item.custPhone}}</view>
						<view class="padding20" v-if="pageIndex == 1">{{item.roomCode}}</view>
					</view>
					<image @click="callPhone(item)" src="/static/salePerson/phone1.png" mode=""></image>
				</view>
				<view class="listBottom noFlexBottom">
					<view class="buyTime_box">
						<view class="buyTime_item">
							<text>{{pageIndex == 0 ? '应认购时间' : '认购时间'}}</text>
							<text class="timeBox black">{{pageIndex == 0 ? item.lastTime : item.subscribleDate}}</text>
						</view>
						<view class="timeLine"></view>
						<view class="buyTime_item">
							<text :class="pageIndex == 1 ? 'red' : ''">{{pageIndex == 0 ? '认购期限' : '失效时间'}}</text>
							<text class="timeBox red">{{item.expiredDate}}</text>
						</view>
					</view>
					<!-- 审核按钮 -->
					<view class="applyBtn">
						<text class="reject" v-if="item.applyStatus==3" @click="rejectTap(item)">驳回原因</text>
						<text class="apply" @click="goDelay(item)">{{(item.applyStatus==0 || item.applyStatus==2)?'申请延期':(item.applyStatus==3?'重新申请':'审核中')}}</text>
					</view>
				</view>
			</view>
		</template>
		<template v-else>
			<image class="noData" src="@/static/img/noData.png" mode=""></image>
		</template>
	</view>
</template>

<script>
	export default {
		props: ['personList','pageIndex'],
		data() {
			return {}
		},
		onLoad(){},
		methods:{
			callPhone(item){
				uni.makePhoneCall({
					phoneNumber: item.custPhone
				});
			},
			// 申请延期
			goDelay(item){
				if(item.applyStatus==0||item.applyStatus==3 || item.applyStatus==2 ){
					uni.navigateTo({
						url:'/pages/salemanVisual/needDeal/applyDelay?data=' + encodeURIComponent(JSON.stringify(item)) + '&pageIndex=' + this.pageIndex
					})
				}
			},
			// 驳回原因
			rejectTap(item){
				uni.showModal({
				    title: '驳回原因',
				    content: item.opinion,
				    showCancel: false,
				    success(res) {
				        if (res.confirm) {
				            successcallback()
				        } else {
				            failcallback()
				        }
				    },
				    fail(res) {}
				})
			}
		}
	}
</script>

<style lang="scss">
	@import '@/styles/personList.scss';
	.buyTime{
		padding-top: 20rpx;
		color: #666;
	}
	.buyTime2 {
		font-size: 24rpx;
	}
	.padding20{
		padding-top: 20rpx;
		color: #666;
	}
	.applyBtn{
		text-align: right;
		margin-top: 30rpx;
		text{
			border: 2rpx solid;
			color: #fff;
			padding: 8rpx 16rpx;
			border-radius: 8rpx;
			font-size: 24rpx!important;
		}
		.reject{
			color: #999!important;
			margin-right: 20rpx;
		}
	}
</style>
